<template>
  <view class="uni-load-more" @click="onClick">
    <!-- #ifdef APP-NVUE -->
    <loading-indicator
      v-if="!webviewHide && status === 'loading' && showIcon"
      :style="{ color: color, width: iconSize + 'px', height: iconSize + 'px' }"
      :animating="true"
      class="uni-load-more__img uni-load-more__img--nvue"
    ></loading-indicator>
    <!-- #endif -->
    <!-- #ifdef H5 -->
    <svg
      width="24"
      height="24"
      viewBox="25 25 50 50"
      v-if="
        !webviewHide &&
          (iconType === 'circle' || (iconType === 'auto' && platform === 'android')) &&
          status === 'loading' &&
          showIcon
      "
      :style="{ width: iconSize + 'px', height: iconSize + 'px' }"
      class="uni-load-more__img uni-load-more__img--android-H5"
    >
      <circle
        cx="50"
        cy="50"
        r="20"
        fill="none"
        :style="{ color: color }"
        :stroke-width="3"
      ></circle>
    </svg>
    <!-- #endif -->
    <!-- #ifndef APP-NVUE || H5 -->
    <view
      v-if="
        !webviewHide &&
          (iconType === 'circle' || (iconType === 'auto' && platform === 'android')) &&
          status === 'loading' &&
          showIcon
      "
      :style="{ width: iconSize + 'px', height: iconSize + 'px' }"
      class="uni-load-more__img uni-load-more__img--android-MP"
    >
      <view :style="{ borderTopColor: color, borderTopWidth: iconSize / 12 }"></view>
      <view :style="{ borderTopColor: color, borderTopWidth: iconSize / 12 }"></view>
      <view :style="{ borderTopColor: color, borderTopWidth: iconSize / 12 }"></view>
    </view>
    <!-- #endif -->
    <!-- #ifndef APP-NVUE -->
    <view
      v-else-if="!webviewHide && status === 'loading' && showIcon"
      :style="{ width: iconSize + 'px', height: iconSize + 'px' }"
      class="uni-load-more__img uni-load-more__img--ios-H5"
    >
      <image
        src="data:image/png;base64,"
        mode="widthFix"
      ></image>
    </view>
    <!-- #endif -->
    <text class="uni-load-more__text" :style="{ color: color }">
      {{
        status === 'more'
          ? contentText.contentdown || i18n.t('core.contentdown')
          : status === 'loading'
          ? contentText.contentrefresh || i18n.t('core.contentrefresh')
          : contentText.contentnomore || i18n.t('core.noMoreData')
      }}
    </text>
  </view>
</template>

<script>
const { platform } = uni.getSystemInfoSync();
export default {
  name: 'UniLoadMore',
  props: {
    status: {
      // 上拉的状态：more-loading前；loading-loading中；noMore-没有更多了
      type: String,
      default: 'more',
    },
    showIcon: {
      type: Boolean,
      default: true,
    },
    iconType: {
      type: String,
      default: 'auto',
    },
    iconSize: {
      type: Number,
      default: 24,
    },
    color: {
      type: String,
      default: '#aaa',
    },
    contentText: {
      type: Object,
      default() {
        return {
          contentdown: '',
          contentrefresh: '',
          contentnomore: '',
        };
      },
    },
  },
  data() {
    return {
      webviewHide: false,
      platform,
    };
  },
  // #ifndef APP-NVUE
  computed: {
    iconSnowWidth() {
      return (Math.floor(this.iconSize / 24) || 1) * 2;
    },
  },
  // #endif
  mounted() {
    // #ifdef APP-PLUS
    const pages = getCurrentPages();
    const page = pages[pages.length - 1];
    const currentWebview = page.$getAppWebview();
    currentWebview.addEventListener('hide', () => {
      this.webviewHide = true;
    });
    currentWebview.addEventListener('show', () => {
      this.webviewHide = false;
    });
    // #endif
  },
  methods: {
    onClick() {
      this.$emit('clickLoadMore', {
        detail: {
          status: this.status,
        },
      });
    },
  },
};
</script>

